import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , App} from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import { Observable } from 'rxjs/Observable';
import {HomePage} from "../home/home";
import {TabPage} from "../tab/tab";
import {OpcoespagePage} from "../opcoespage/opcoespage";

@IonicPage()
@Component({
  selector: 'page-perfil',
  templateUrl: 'perfil.html',
})
export class PerfilPage {
  usuario: Observable<any[]>; 
  

  constructor(public navCtrl: NavController, public navParams: NavParams, private afAuth: AngularFireAuth, private database : AngularFireDatabase, private app: App ) {
    this.afAuth.authState.subscribe(data => {
      if (data && data.email && data.uid) {
        this.usuario = database.object("users/" + data.uid).valueChanges();
        
      }
    });
    
  }


  
  opcao(){
    this.navCtrl.push(OpcoespagePage);
  }



  ionViewDidLoad() {
    console.log('ionViewDidLoad PerfilPage');
  }

}



